.mainWrapper{
    margin-left: 60px;
}

.wrapper{
    display: grid;
    grid-template-columns: 400px 400px;
    gap:30px;
}

.block{
    border: 1px solid red;
    padding: 15px;
    border-radius: 10px;
}

.formWrapper{
    display: grid;
    grid-template-columns: 50px 1fr;
    gap:15px;
    margin-bottom: 15px;
}